/* eslint-disable func-call-spacing */
<template>
  <div class="study">
    <el-container>
           <el-header>
                  <el-row>
                    <el-col :span='18'>
                    <el-menu  class="el-menu-demo" mode="horizontal" text-color="#409EFF" background-color="#ffffff" style="border-bottom-color:#909399">
                    <el-menu-item index="1"><el-link href="http://localhost:8080/#/index" >程序设计语言教程案例图示化演示系统</el-link></el-menu-item>
                    <el-menu-item index="2"><el-link href="http://localhost:8080/#/study" >课程</el-link></el-menu-item>
                    <el-menu-item index="3" ><el-link href="http://localhost:8080/#/class" >班级</el-link></el-menu-item>
                    <el-menu-item index="4" ><el-link href="http://localhost:8080/#/bbs" >论坛</el-link></el-menu-item>
                    </el-menu>
                    </el-col>
                    <el-col :span='6'>
                      <el-menu  :default-active="7" class="el-menu-demo" mode="horizontal" text-color="#000" background-color="#ffffff" style="border-bottom-color:#909399">
                        <el-menu-item index="5"><el-link href="http://localhost:8080/#/home" ><i class="el-icon-bell"></i></el-link></el-menu-item>
                        <el-menu-item index="6"><el-link href="http://localhost:8080/#/home" ><i class="el-icon-setting"></i></el-link></el-menu-item>
                        <el-menu-item index="7"><el-link href="http://localhost:8080/#/home" ><el-avatar src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png"></el-avatar>{{username}}</el-link></el-menu-item>
                      </el-menu>
                    </el-col>
                  </el-row>
                    <!-- <el-menu-item index="4"><a href="https://www.ele.me" >订单管理</a></el-menu-item> -->
            </el-header>
            <el-container>
            <el-aside width="200px">
                <div style="border-top-color:#909399;">
                <div style='background-color:#909399'>
                    <div style='height:30px;'></div>
                    <el-avatar :size="50" :src="circleUrl"></el-avatar>
                    <div style='height:45px;'></div>
                </div></div>
                    <h1>{{username}}同学，您好</h1>
                <el-menu default-active="1" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose" style="border-right-color:#ffffff">
                     <el-menu-item index="1">
                        <i class="el-icon-user"></i>
                        <span slot="title">个人信息</span>
                        <i class="el-icon-arrow-righ"></i>
                    </el-menu-item>
                    <el-menu-item index="2">
                        <i class="el-icon-lock"></i>
                        <span slot="title">密码修改</span>
                        <i class="el-icon-arrow-righ"></i>
                    </el-menu-item>
                    <el-menu-item index="3" >
                        <i class="el-icon-document"></i>
                        <span slot="title">历史记录</span>
                        <i class="el-icon-arrow-righ"></i>
                    </el-menu-item>
                    <el-menu-item index="4">
                        <i class="el-icon-delete"></i>
                        <span slot="title">注销账号</span>
                        <i class="el-icon-arrow-righ"></i>
                    </el-menu-item>
                    </el-menu>
            </el-aside>
            <el-main>
                <el-row><el-col :span='6'>
                    <div style="height:200px,width:200px"> <el-divider direction="vertical"></el-divider></div>
                    </el-col>
               <el-col :span='8'>
                <el-card>
                <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                    <el-form-item label="账户名" prop="name">
                        <el-input v-model="ruleForm.name"></el-input>
                    </el-form-item>
                    <el-form-item label="性别" prop="region">
                        <el-select v-model="ruleForm.region" placeholder="请选择性别">
                        <el-option label="男" value="man"></el-option>
                        <el-option label="女" value="woman"></el-option>
                         <el-option label="隐藏" value="unknown"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="学号" prop="UNo">
                        <el-input v-model="ruleForm.UNo"></el-input>
                    </el-form-item>
                    <el-form-item label="真实姓名" prop="truename">
                        <el-input v-model="ruleForm.truename"></el-input>
                    </el-form-item>
                    <el-form-item label="邮箱" prop="email">
                        <el-input v-model="ruleForm.email"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="submitForm('ruleForm')">提交修改</el-button>
                    </el-form-item>
                    </el-form></el-card>
                </el-col></el-row>
            </el-main>
             </el-container>
        </el-container>
  </div>
</template>

<script>
export default {
  name: 'study',
  data () {
    return {
      webname: '程序设计语言教程案例图示化演示系统|身份认证',
      username: '',
      circleUrl: 'https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png',
      ruleForm: {
        name: '',
        region: '',
        UNo: '',
        truename: '',
        email: ''
      },
      rules: {
        name: [
          { required: true, message: '请输入昵称', trigger: 'blur' }
        ],
        region: [
          { required: true, message: '请选择性别', trigger: 'change' }
        ],
        UNo: [
          { required: false, message: '请输入学号', trigger: 'change' }
        ],
        truename: [
          { required: false, message: '', trigger: 'change' }
        ],
        email: [
          { required: false, message: '请输入邮箱', trigger: 'change' }
        ]}

    }
  },
  methods: {
    handleClick (tab, event) {
      console.log(tab, event)
    },
    exit () {
      // document.cookie = this.username + '=; expires=Thu, 01 Jan 1970 00:00:00 GMT"'
      // location.replace('http://localhost:8080/#/login')
    },
    open (info, infotitle) {
      this.$alert(info, infotitle, {
        confirmButtonText: '确定',
        callback: action => {
          this.$message({
            type: 'info',
            message: info
          })
        }
      })
    },
    setCookie (cname, cvalue, exdays) {
      var d = new Date()
      d.setTime(d.getTime() + (exdays * 24 * 60 * 60 * 1000))
      var expires = 'expires=' + d.toGMTString()
      document.cookie = cname + '=' + cvalue + '; ' + expires
    },
    getCookie (cname) {
      var name = cname + '='
      var ca = document.cookie.split(';')
      for (var i = 0; i < ca.length; i++) {
        var c = ca[i].trim()
        if (c.indexOf(name) === 0) return c.substring(name.length, c.length)
      }
      return ''
    },
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert('submit!')
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    checklogin () { // 登陆函数
      if (this.username === '') {
        var ca = document.cookie.split(';')
        var name = ca[ca.length - 1].split('=')
        this.username = name[0]
        console.log(ca)
      }
      document.title = '个人中心|程序设计语言教程案例图示化演示系统'
    }
  },
  mounted: function () {
    this.checklogin() // 需要触发的函数
  }
}

</script>
<link rel="stylesheet" href="https://unpkg.com/element-ui/lib/theme-chalk/index.css">;
<script src="https://unpkg.com/element-ui/lib/index.js"></script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.login{
    color:black;
    background-color: rgb(255, 248, 220);
    text-align: left;
    }
.el-header, .el-footer {
    background-color: #409EFF;
    color: #333;
    text-align: center;
    line-height: 60px;
  }
.el-main {
    background-color: #E9EEF3;
    color: #333;
    text-align: center;
    line-height: 160px;
  }
    .item {
    margin-bottom: 18px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }
  .box-card {
    height: 200px;
  }
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }

</style>



