/* eslint-disable func-call-spacing */
<template>
  <div class="index">
    <el-container>
           <el-header>
                  <el-row>
                    <el-col :span='18'>
                    <el-menu :default-active="1" class="el-menu-demo" mode="horizontal" text-color="#409EFF" background-color="#ffffff" style="border-bottom-color:#909399">
                    <el-menu-item index="1"><el-link href="http://localhost:8080/#/index" ><b>程序设计语言教程案例图示化演示系统</b></el-link></el-menu-item>
                    <el-menu-item index="2"><el-link href="http://localhost:3000/studylesson" >课程</el-link></el-menu-item>
                    <el-menu-item index="3" ><el-link href="http://localhost:8080/#/class" >班级</el-link></el-menu-item>
                    <el-menu-item index="4" ><el-link href="http://localhost:8080/#/bbs" >论坛</el-link></el-menu-item>
                    </el-menu>
                    </el-col>
                    <el-col :span='6'>
                      <el-menu class="el-menu-demo" mode="horizontal" text-color="#000" background-color="#ffffff" style="border-bottom-color:#909399">
                        <el-menu-item index="5"><el-link href="http://localhost:8080/#/home" ><i class="el-icon-bell"></i></el-link></el-menu-item>
                        <el-menu-item index="6"><el-link href="http://localhost:8080/#/home" ><i class="el-icon-setting"></i></el-link></el-menu-item>
                        <el-menu-item index="7"><el-link href="http://localhost:8080/#/home" ><el-avatar src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png"></el-avatar>{{username}}</el-link></el-menu-item>
                      </el-menu>
                    </el-col>
                  </el-row>
                    <!-- <el-menu-item index="4"><a href="https://www.ele.me" >订单管理</a></el-menu-item> -->
            </el-header>
            <el-main>
                <el-row>
                    <el-col :span='16'>
                        <!-- 跑马灯部分 -->
                     <el-card class="box-card" shadow="always">
                        <el-carousel height="400px">
                            <el-carousel-item v-for="item in 4" :key="item">
                               <el-image
                                style="width: 100%; height: 100%"
                                :src="headlineurl"
                                :fit="fill"></el-image>
                            </el-carousel-item>
                        </el-carousel>
                     </el-card>
                    </el-col>
                    <el-col :span='1'>
                        <div style='width:300px;height:400px;'></div>
                    </el-col>
                    <el-col :span='6'>
                        <!-- 个人信息部分 -->
                        <div style='background-color:"#fff123"height:400px;'>
                        <el-card class="box-card" shadow="always"  >
                            <div style=';background-color:#909399'>
                                <div style='height:30px;'></div>
                                <el-avatar :size="50" :src="circleUrl"></el-avatar>
                                <div style='height:45px;'></div>
                            </div>
                            <h1>{{username}}同学，您好</h1>
                            <div style='height:50px;'>
                                <el-tag type="info" style="width:80%"><el-link href="http://localhost:8080/#/home" >个人信息</el-link></el-tag>
                            </div>
                            <div style='height:50px;'>
                                <el-tag type="info" style="width:80%"><el-link href="http://localhost:8080/#/study" >学习情况</el-link></el-tag>
                            </div>
                            <div style='height:50px;'>
                                <el-tag type="info" style="width:80%"><el-link href="http://localhost:8080/#/message" >我的消息</el-link></el-tag>
                            </div>
                             <div style='height:50px;'>
                                <el-tag type="info" style="width:80%" @click="exit"><el-link href="http://localhost:8080/#/login">退出登陆</el-link></el-tag>
                            </div>
                        </el-card>
                        </div>
                    </el-col>
                </el-row>
            </el-main>
        </el-container>
  </div>
</template>

<script>
export default {
  name: 'index',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      webname: '程序设计语言教程案例图示化演示系统|身份认证',
      username: '',
      circleUrl: 'https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png',
      headlineurl: './static/headline1.jpg'
    }
  },
  methods: {
    handleClick (tab, event) {
      console.log(tab, event)
    },
    exit () {
      // document.cookie = this.username + '=; expires=Thu, 01 Jan 1970 00:00:00 GMT"'
      // location.replace('http://localhost:8080/#/login')
    },
    open (info, infotitle) {
      this.$alert(info, infotitle, {
        confirmButtonText: '确定',
        callback: action => {
          this.$message({
            type: 'info',
            message: info
          })
        }
      })
    },
    setCookie (cname, cvalue, exdays) {
      var d = new Date()
      d.setTime(d.getTime() + (exdays * 24 * 60 * 60 * 1000))
      var expires = 'expires=' + d.toGMTString()
      document.cookie = cname + '=' + cvalue + '; ' + expires
    },
    getCookie (cname) {
      var name = cname + '='
      var ca = document.cookie.split(';')
      for (var i = 0; i < ca.length; i++) {
        var c = ca[i].trim()
        if (c.indexOf(name) === 0) return c.substring(name.length, c.length)
      }
      return ''
    },
    checklogin () { // 登陆函数
      if (this.username === '') {
        var ca = document.cookie.split(';')
        var name = ca[ca.length - 1].split('=')
        this.username = name[0]
        console.log(ca)
      }
      document.title = '主页|程序设计语言教程案例图示化演示系统'
    }
  },
  mounted: function () {
    this.checklogin() // 需要触发的函数
  }
}

</script>
<link rel="stylesheet" href="https://unpkg.com/element-ui/lib/theme-chalk/index.css">;
<script src="https://unpkg.com/element-ui/lib/index.js"></script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.login{
    color:black;
    background-color: rgb(255, 248, 220);
    text-align: left;
    }
.el-header, .el-footer {
    background-color: #409EFF;
    color: #333;
    text-align: center;
    line-height: 60px;
  }
.el-main {
    background-color: #E9EEF3;
    color: #333;
    text-align: center;
    line-height: 160px;
  }
.haed{
    color:#409EFF
}
.el-carousel__item h3 {
    color: #f7f9fc;
    background-color: #404750;
    font-size: 20px;
    opacity: 0.75;
    line-height: 250px;
    margin: 0;
  }

.el-carousel__item:nth-child(2n) {
     background-color: #99a9bf;
  }
.el-carousel__item:nth-child(2n+1) {
     background-color: #2b2e31;
  }
</style>



