/* eslint-disable func-call-spacing */
<template>
  <div class="quan">
    <el-container>
      <el-header>
        <el-row>
          <el-col :span="18">
            <el-menu
              :default-active="3"
              class="el-menu-demo"
              mode="horizontal"
              text-color="#000"
              background-color="#ffffff"
              style="border-bottom-color: #909399"
            >
              <el-menu-item index="1"> {{ quanname }}</el-menu-item>
            </el-menu>
          </el-col>
          <el-col :span="6">
            <el-menu
              class="el-menu-demo"
              mode="horizontal"
              text-color="#409EFF"
              background-color="#ffffff"
              style="border-bottom-color: #909399"
            >
              <el-menu-item index="2"
                ><el-link
                  href="http://localhost:8080/#/index"
                  style="text-align: right"
                  >目录</el-link
                ></el-menu-item
              >
            </el-menu>
          </el-col>
        </el-row>
        <!-- <el-menu-item index="4"><a href="https://www.ele.me" >订单管理</a></el-menu-item> -->
      </el-header>
      <el-main>
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span>{{ quanname }}</span>
            <span style="float: right; padding: 3px 0" type="text">
              可使用次数：{{ usenum }}
            </span>
          </div>
          <div class="text item">
            <div slot="header" class="clearfix">
              <span style="padding: 3px 0; font-size: 20px" type="text"
                >{{ descri }}
              </span>
            </div>
            <el-image
              style="width: 400px; height: 400px"
              :src="url"
              :fit="contain"
            ></el-image>
          </div>
        </el-card>
      </el-main>
      <el-footer style="position: fixed; bottom: 30px; left: 25px">
        <el-row>
          <el-col :span="12">
            <el-card class="box-card">
              <div class="clearfix">
                <el-button
                  type="danger"
                  @click="use"
                  style="width: 100%"
                  size="big"
                  >&nbsp;&nbsp;&nbsp;使用&nbsp;&nbsp;&nbsp;</el-button
                >
              </div>
            </el-card>
          </el-col>
          <el-col :span="12">
            <el-card class="box-card">
              <div class="clearfix">
                <el-button type="warning" @click="next" style="width: 100%"
                  >&nbsp;&nbsp;&nbsp;下一张&nbsp;&nbsp;&nbsp;</el-button
                >
              </div>
            </el-card>
          </el-col>
        </el-row>
      </el-footer>
    </el-container>
  </div>
</template>

<script>
export default {
  name: "quan",
  data() {
    return {
      quanname: "友爱券",
      usenum: "5",
      descri: "可以奖励亲亲一次",
      msg: "Welcome to Your Vue.js App",
      webname: "程序设计语言教程案例图示化演示系统|身份认证",
      username: "",
      circleUrl:
        "https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png",
      url: "./static/test.jpg",
      classname: "",
      teacher: "",
      createtime: "",
    };
  },
  methods: {
    handleClick(tab, event) {
      console.log(tab, event);
    },
    exit() {
      // document.cookie = this.username + '=; expires=Thu, 01 Jan 1970 00:00:00 GMT"'
      // location.replace('http://localhost:8080/#/login')
    },
    open(info, infotitle) {
      this.$alert(info, infotitle, {
        confirmButtonText: "确定",
        callback: (action) => {
          this.$message({
            type: "info",
            message: info,
          });
        },
      });
    },
    setCookie(cname, cvalue, exdays) {
      var d = new Date();
      d.setTime(d.getTime() + exdays * 24 * 60 * 60 * 1000);
      var expires = "expires=" + d.toGMTString();
      document.cookie = cname + "=" + cvalue + "; " + expires;
    },
    getCookie(cname) {
      var name = cname + "=";
      var ca = document.cookie.split(";");
      for (var i = 0; i < ca.length; i++) {
        var c = ca[i].trim();
        if (c.indexOf(name) === 0) return c.substring(name.length, c.length);
      }
      return "";
    },
    checklogin() {
      // 登陆函数
      if (this.username === "") {
        var ca = document.cookie.split(";");
        var name = ca[ca.length - 1].split("=");
        this.username = name[0];
        console.log(ca);
      }
      document.title = "班级|程序设计语言教程案例图示化演示系统";
      this.classname = "通信1603";
      this.teacher = "xxx教师";
      this.createtime = "2020-04-10";
    },
  },
  mounted: function () {
    this.checklogin(); // 需要触发的函数
  },
};
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<link rel="stylesheet" href="https://unpkg.com/element-ui/lib/theme-chalk/index.css">;
<script src="https://unpkg.com/element-ui/lib/index.js"></script>
<style scoped>
.login{
    color:black;
    background-color: rgb(255, 248, 220);
    text-align: left;
    }
.el-header, .el-footer {
    background-color: #409EFF;
    color: #333;
    text-align: center;
    line-height: 60px;
  }
.el-main {
    background-color: #E9EEF3;
    color: #333;
    text-align: center;
    line-height: 160px;
  }
    .item {
    margin-bottom: 18px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }
  .box-card {
    height: 200px;
  }
  .coursespic {
    background-color: darkgray;
    height: 150px;
    width: 200px;
  }
</style>



