/* eslint-disable func-call-spacing */
<template>
  <div class="myclass">
    <el-container>
           <el-header>
                  <el-row>
                    <el-col :span='18'>
                    <el-menu  :default-active="3" class="el-menu-demo" mode="horizontal" text-color="#409EFF" background-color="#ffffff" style="border-bottom-color:#909399">
                    <el-menu-item index="1"><el-link href="http://localhost:8080/#/index" >程序设计语言教程案例图示化演示系统</el-link></el-menu-item>
                    <el-menu-item index="2"><el-link href="http://localhost:8080/#/study" >课程</el-link></el-menu-item>
                    <el-menu-item index="3" ><el-link href="http://localhost:8080/#/class" ><b>班级</b></el-link></el-menu-item>
                    <el-menu-item index="4" ><el-link href="http://localhost:8080/#/bbs" >论坛</el-link></el-menu-item>
                    </el-menu>
                    </el-col>
                    <el-col :span='6'>
                      <el-menu class="el-menu-demo" mode="horizontal" text-color="#000" background-color="#ffffff" style="border-bottom-color:#909399">
                        <el-menu-item index="5"><el-link href="http://localhost:8080/#/home" ><i class="el-icon-bell"></i></el-link></el-menu-item>
                        <el-menu-item index="6"><el-link href="http://localhost:8080/#/home" ><i class="el-icon-setting"></i></el-link></el-menu-item>
                        <el-menu-item index="7"><el-link href="http://localhost:8080/#/home" ><el-avatar src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png"></el-avatar>{{username}}</el-link></el-menu-item>
                      </el-menu>
                    </el-col>
                  </el-row>
                    <!-- <el-menu-item index="4"><a href="https://www.ele.me" >订单管理</a></el-menu-item> -->
            </el-header>
            <el-main>
                 <el-card class="box-card">
                <div slot="header" class="clearfix">
                      <span>我加入的班级</span>
                      <el-button style="float: right; padding: 3px 0" type="text">
                        <el-link href="http://localhost:8080/#/class/myclass" >其他班级
                        </el-link>
                      </el-button>
                      </div>
                <div  class="text item">
                  <el-row :gutter="20">
                    <el-col :span='10'>
                      <el-image
                        style="width: 400px; height: 250px"
                        :src="headlineurl"
                        :fit="fill"></el-image>
                    </el-col>
                    <el-col :span='7'>
                        <div style="text-align: left;">
                            <h2>{{classname}}</h2>
                            <h5>{{teacher}}</h5>
                            <p>创立于{{createtime}}</p>
                        </div>
                    </el-col>
                    <el-col :span='7'>
                      <el-card class="box-card">
                      <div   class="text item">
                          <el-table :data="classmate" stripe style="width: 100%" height="250">
                          <el-table-column
                            prop="classmate"
                            label="班级成员"
                            width="180">
                          </el-table-column>
                          </el-table>
                      </div>
                      </el-card>
                    </el-col>
                  </el-row>
                </div>
                  </el-card>
                  <el-row :gutter="20">
                    <el-col :span='12'>
                      <el-card class="box-card">
                        <div slot="header" class="clearfix">
                      <span>正在进行的任务</span>
                      <el-button style="float: right; padding: 3px 0" type="text">
                        <el-link href="http://localhost:8080/#/class/classA" >全部任务
                        </el-link>
                      </el-button>
                      </div>
                      <div   class="text item">
                         <el-table :data="taskData" stripe style="width: 100%">
                          <el-table-column
                            prop="taskname"
                            label="任务名称"
                            width="180">
                          </el-table-column>
                          <el-table-column
                            prop="deadtime"
                            label="截止时间">
                          </el-table-column>
                          <el-table-column
                            fixed="right"
                            label="操作"
                            width="100">
                            <template slot-scope="scope">
                              <el-button @click="handleClick(scope.row)" type="text" size="small">查看</el-button>
                              <el-button type="text" size="small">提交</el-button>
                            </template>
                          </el-table-column>
                        </el-table>
                      </div>
                      </el-card>
                    </el-col>
                    <el-col :span='12'>
                      <el-card class="box-card">
                        <div slot="header" class="clearfix">
                          <span>课程列表</span>
                          <el-button style="float: right; padding: 3px 0" type="text">
                            <el-link href="http://localhost:8080/#/class/classA" >全部课程
                            </el-link>
                          </el-button>
                        </div>
                      <div   class="text item">
                      <el-table :data="classData" stripe style="width: 100%">
                          <el-table-column
                            prop="classname"
                            label="课程名称"
                            width="100">
                          </el-table-column>
                          <el-table-column
                            prop="require"
                            label="课程要求"
                            width="180">
                          </el-table-column>
                          <el-table-column
                            prop="deadtime"
                            label="截止时间">
                          </el-table-column>
                          <el-table-column
                            fixed="right"
                            label="操作"
                            width="100">
                            <template slot-scope="scope">
                              <el-button @click="handleClick(scope.row)" type="text" size="small">查看</el-button>
                            </template>
                          </el-table-column>
                        </el-table>
                      </div>
                      </el-card>
                    </el-col>
                  </el-row>
            </el-main>
        </el-container>
  </div>
</template>

<script>
export default {
  name: 'myclass',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      webname: '程序设计语言教程案例图示化演示系统|身份认证',
      username: '',
      circleUrl: 'https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png',
      headlineurl: './static/myclass.png',
      classname: '',
      teacher: '',
      createtime: '',
      classmate: [{
        classmate: '同学A'
      }, {
        classmate: '同学B'
      }, {
        classmate: '同学C'
      }, {
        classmate: '同学D'
      }, {
        classmate: '同学E'
      }],
      classData: [{
        classname: '课程A',
        deadtime: '2020-5-24',
        require: '完成学习，提交笔记'
      }, {
        classname: '课程B',
        deadtime: '2020-5-30',
        require: '完成学习'
      }, {
        classname: '课程C',
        deadtime: '2020-6-05',
        require: '完成学习，参与讨论'
      }, {
        classname: '课程D',
        deadtime: '2020-6-10',
        require: '完成学习，参与讨论'
      }],
      taskData: [{
        taskname: '完成课程A的学习',
        deadtime: '2020-5-24'
      }, {
        taskname: '完成课程B的学习',
        deadtime: '2020-5-24'
      }, {
        taskname: '提交学习笔记',
        deadtime: '2020-5-20'
      }, {
        taskname: '完成课程C的学习',
        deadtime: '2020-5-24'
      }
      ]}
  },
  methods: {
    handleClick (tab, event) {
      console.log(tab, event)
    },
    exit () {
      // document.cookie = this.username + '=; expires=Thu, 01 Jan 1970 00:00:00 GMT"'
      // location.replace('http://localhost:8080/#/login')
    },
    open (info, infotitle) {
      this.$alert(info, infotitle, {
        confirmButtonText: '确定',
        callback: action => {
          this.$message({
            type: 'info',
            message: info
          })
        }
      })
    },
    setCookie (cname, cvalue, exdays) {
      var d = new Date()
      d.setTime(d.getTime() + (exdays * 24 * 60 * 60 * 1000))
      var expires = 'expires=' + d.toGMTString()
      document.cookie = cname + '=' + cvalue + '; ' + expires
    },
    getCookie (cname) {
      var name = cname + '='
      var ca = document.cookie.split(';')
      for (var i = 0; i < ca.length; i++) {
        var c = ca[i].trim()
        if (c.indexOf(name) === 0) return c.substring(name.length, c.length)
      }
      return ''
    },
    checklogin () { // 登陆函数
      if (this.username === '') {
        var ca = document.cookie.split(';')
        var name = ca[ca.length - 1].split('=')
        this.username = name[0]
        console.log(ca)
      }
      document.title = '班级|程序设计语言教程案例图示化演示系统'
      this.classname = '通信1603'
      this.teacher = 'xxx教师'
      this.createtime = '2020-04-10'
    }
  },
  mounted: function () {
    this.checklogin() // 需要触发的函数
  }
}

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<link rel="stylesheet" href="https://unpkg.com/element-ui/lib/theme-chalk/index.css">;
<script src="https://unpkg.com/element-ui/lib/index.js"></script>
<style scoped>
.login{
    color:black;
    background-color: rgb(255, 248, 220);
    text-align: left;
    }
.el-header, .el-footer {
    background-color: #409EFF;
    color: #333;
    text-align: center;
    line-height: 60px;
  }
.el-main {
    background-color: #E9EEF3;
    color: #333;
    text-align: center;
    line-height: 160px;
  }
    .item {
    margin-bottom: 18px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }
  .box-card {
    height: 200px;
  }
  .coursespic {
    background-color: darkgray;
    height: 150px;
    width: 200px;
  }
</style>



