<script>
export default {
  name: 'study',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      webname: '程序设计语言教程案例图示化演示系统|身份认证',
      username: '',
      circleUrl: 'https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png',
      tableData: [{
        classname: '课程A',
        process: '100%',
        lasttime: '2020-4-20'
      }, {
        classname: '课程B',
        process: '80%',
        lasttime: '2020-4-21'
      }, {
        classname: '课程C',
        process: '60%',
        lasttime: '2020-4-23'
      }, {
        classname: '课程D',
        process: '10%',
        lasttime: '2020-4-01'
      }],
      noteData: [{
        notename: '笔记A',
        process: '课程A',
        lasttime: '2020-4-23'
      }, {
        notename: '笔记B',
        process: '课程B',
        lasttime: '2020-4-22'
      }, {
        notename: '笔记C',
        process: '课程C',
        lasttime: '2020-4-10'
      }, {
        notename: '笔记D',
        process: '课程D',
        lasttime: '2020-4-05'
      }]
    }
  },
  methods: {
    handleClick (tab, event) {
      console.log(tab, event)
    },
    exit () {
      // document.cookie = this.username + '=; expires=Thu, 01 Jan 1970 00:00:00 GMT"'
      // location.replace('http://localhost:8080/#/login')
    },
    open (info, infotitle) {
      this.$alert(info, infotitle, {
        confirmButtonText: '确定',
        callback: action => {
          this.$message({
            type: 'info',
            message: info
          })
        }
      })
    },
    setCookie (cname, cvalue, exdays) {
      var d = new Date()
      d.setTime(d.getTime() + (exdays * 24 * 60 * 60 * 1000))
      var expires = 'expires=' + d.toGMTString()
      document.cookie = cname + '=' + cvalue + '; ' + expires
    },
    getCookie (cname) {
      var name = cname + '='
      var ca = document.cookie.split(';')
      for (var i = 0; i < ca.length; i++) {
        var c = ca[i].trim()
        if (c.indexOf(name) === 0) return c.substring(name.length, c.length)
      }
      return ''
    },
    checklogin () { // 登陆函数
      if (this.username === '') {
        var ca = document.cookie.split(';')
        var name = ca[ca.length - 1].split('=')
        this.username = name[0]
        console.log(ca)
      }
      document.title = '课程中心|程序设计语言教程案例图示化演示系统'
    }
  },
  mounted: function () {
    this.checklogin() // 需要触发的函数
  }
}

</script>
